@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.crearInvitacion{
    background-color: v.$blanco;
    padding: 4rem;
    box-shadow: 10px 10px 8px -3px rgba(0,0,0,0.33);
    .crear__titulo, .enviar__titulo{
        font-size: 2rem;
        margin-top: 5rem;
    }
    .enviar__titulo{
        margin-bottom: 3rem;
    }
    .crear{
        label{
            font-size: 1.6rem;
        }
        .confirm__btn{
            @include m.button(v.$primario, v.$secundario, v.$blanco, v.$negro);
        }
        @include m.tablet{
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        .Formulario{
            input, textarea, label, select{
                min-width: 30rem;
                @include m.tablet{
                    min-width: 50rem;
                }
            }
        }
    }
    .enviar{
        .enviar__flex{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
        label{
            font-size: 1.6rem;
        }
        .aggInvitado__btn{
            @include m.button(v.$primario, v.$secundario, v.$blanco, v.$negro);
        }
        @include m.tablet{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
        .Formulario{
            input, textarea, label, select{
                min-width: 30rem;
                @include m.tablet{
                    min-width: 10rem;
                }
            }   
        }
        .containerFlex{
            display: flex;
            flex-direction: column;
            .form-group{
                @include m.tablet{
                    min-width: 25rem;
                    max-width: 100%;
                }
            }
            .Selecccion{
                min-width: 30rem;
                @include m.tablet{
                //    width: 10rem;
                   min-width: 0;
                }
            }
            @include m.tablet{
                flex-direction: row;
            }
        }
    }
    .containerListaInvitados{
        .campo{
            display: flex;
            gap: 4rem;
            justify-content: center;
            align-items: center;
            text-align: center;
            margin-bottom: 1rem;
            p{
                font-size: 1.4rem;
                min-width: 12.9rem;
            }
        }
    }
}