@use 'Base/variables' as v;
@use 'Base/mixins' as m;

body{
    background-color: v.$bg-color;
}
//Ajustando tamao de cabeceras a contenido
.content {
    .__Tabla {
        .tabla {
            .tabla__cabecera{
                tr{
                    .cabecera{
                        width: fit-content;
                        padding: 1rem;
                        text-align: center;
                        vertical-align: middle;
                    }
                }
            }
        }
    }
}


//Para centrar todo
.content {
    .__Tabla {
        .tabla {
            .tabla__cuerpo {
                tr {
                    .id,
                    .reunion,
                    .fecha,
                    .hora,
                    .nombreAanfi,
                    .acciones{
                        text-align: center;
                        vertical-align: middle;
                        padding: 1rem 4rem 1rem 4rem;
                    }
                }
            }
        }
    }
}

//Para centrar todo
.content {
    .__Tabla {
        .tabla {
            .tabla__cabecera,
            .tabla__cuerpo {
                tr {
                    .acciones {
                        text-align: center;
                        vertical-align: middle;
                        padding: 0rem;
                    }
                }
            }
        }
    }
}

